<template>
    <form class="wrrite_art">
        <div class="header">{{!edit?typetitle[0]:typetitle[1]}}</div>
        <div class="wrrite_hd">
            <div class="hd_fiexd">
                <div class="input_line">
                    <label class="label_name">:</label>
                    <a-input placeholder="" v-model="artdata.title" />
                </div>
                <div class="input_line">
                    <label class="label_name">:</label>
                    <a-input placeholder="" v-model="artdata.stitle"/>
                </div>
                <div class="input_line">
                    <label class="label_name">:</label>
                    <a-input placeholder="" v-model="artdata.author"/>
                </div>
                <div class="input_line">
                    <label class="label_name">:</label>
                    <a-select v-model="artdata.cate_id" :defaultValue="artdata.cate_id">
                        <a-select-option :value="String(item.id)" :title="item.catename" v-for="item in catelistdata" :key="item.id">{{item.catename}}</a-select-option>
                    </a-select>
                </div>
            </div>
            <div class="hd_fiexd">
                <div class="input_line">
                    <label class="label_name">vip:</label>
                    <a-radio-group style="padding: 5px 0 0 0;" :options="is_vip" v-model="artdata.is_vip" @change="onChangeVip"/>
                </div>
                <div class="input_line">
                    <label class="label_name">:</label>
                    <a-radio-group style="padding: 5px 0 0 0;" :options="is_recomend" v-model="artdata.is_recomon" @change="onChangeRecomon"/>
                </div>
                <div class="input_line">
                    <label class="label_name">:</label>
                    <a-radio-group style="padding: 5px 0 0 0;" :options="arttype" v-model="artdata.type" @change="onChangeArtType"/>
                </div>
                <div class="input_line">
                    <label class="label_name">:</label>
                    <a-select v-model="artdata.tag_id" :defaultValue="artdata.cate_id">
                        <a-select-option :value="String(item.id)" :title="item.tagname" v-for="item in tagdatalist" :key="item.id">{{item.tagname}}</a-select-option>
                    </a-select>
                </div>
            </div>
        </div>
        <div class="input_line">
            <label class="label_name">:</label>
            <a-textarea placeholder="" :autosize="{ minRows: 2, maxRows: 6 }" v-model="artdata.descs" />
        </div>
        <div class="input_line">
            <label class="label_name">:</label>
            <mavon-editor 
            @change="changeData"
            v-model ="artdata.marktext"
            style="min-height:580px;z-index:0;width:100%"
            :ishljs ="true" ></mavon-editor>
        </div>
        <div class="input_line">
            <label class="label_name">:</label>
            <a-input placeholder="" v-model="artdata.path"/>
        </div>
        <div class="input_line">
            <label class="label_name">:</label>
            <a-input placeholder="" v-model="artdata.link_url"/>
        </div>
        <div class="btn_div">
            <a-button type="primary" style="margin-right:7px;" @click="submitArt"></a-button>
            <a-button @click="resetData"></a-button>
        </div> 
    </form>
</template>
<script>
import Vue from 'vue'
import 'mavon-editor/dist/css/index.css'
import {getCate,getTagsList} from '../../apicontrol/apilist.js'
import mavonEditor from 'mavon-editor'
Vue.use(mavonEditor)
export default {
    props:{
        wrrittedata:{
            type:Object
        },
        edit:{
            type:Boolean,
            default(){
                return false
            }
        }
    },
    data() {
        return {
            artdata:{
                title:'',
                stitle:'',
                descs:'',
                cate_id:'',
                tag_id:'',
                author:'',
                type:'0',
                content:'',
                marktext:'',
                is_recomon:'0',
                is_vip:'0',
                path:'',
                link_url:"",
            },
            typetitle:["",""],
            is_vip:[{ label: '', value: '1' },{ label: '', value: '0' }],
            is_recomend:[{ label: '', value: '1' },{ label: '', value: '0' }],
            arttype:[{ label: '', value: '0' },{ label: '', value: '1' },{ label: '', value: '2' },{ label: '', value: '3' }],
            catelistdata:[],
            tagdatalist:[],
        }
    },
    watch : {
        wrrittedata(value,oldvalue){
            //console.log(this.wrrittedata)
            for(let i in this.wrrittedata){
                if(typeof(this.wrrittedata[i]) == 'number'){
                    this.wrrittedata[i] = `${this.wrrittedata[i]}`
                }
            }
            Object.assign(this.artdata,this.wrrittedata)
            //console.log(this.artdata)
            //this.changeData(this.artdata.marktext,this.artdata.content)
        }
    },
    created(){
        this.getSelectCate('init');
        this.getTagsList('init');
        Object.assign(this.artdata,this.wrrittedata)
    },
    methods: {
        onChangeVip(e){
            this.artdata.is_vip = e.target.value
        },
        onChangeRecomon(e){
            this.artdata.is_recomon = e.target.value
        },
        onChangeArtType(e){
            this.artdata.type = e.target.value
        },
        changeData(value, render) {
            this.artdata.marktext = value;
            this.artdata.content = render;
        },
        //
        submitArt(){
            let data = this.artdata;
            //console.log(data);
            this.$emit("getArtData",data);
        },
        //
        resetData(){
            Object.assign(this.artdata,this.wrrittedata)
        },
        //
        getSelectCate(type){
            getCate({type:"select"}).then(res=>{
                this.catelistdata = res.data;
                if(type == 'init'){
                    this.artdata.cate_id = String(this.catelistdata['0'].id);
                }
            })
        },
        //
        getTagsList(type){
            getTagsList().then(res=>{
                this.tagdatalist = res.data;
                if(type == 'init'){
                    this.artdata.tag_id = String(this.tagdatalist['0'].id);
                }
            })
        }
        //

    },

}
</script>
<style lang="less" scoped>
.wrrite_art{
    font-size: 14px;
    .btn_div{
        width: 100%;
        box-sizing:border-box;
        padding-right: 20px;
        text-align: right;
    }
    .header{
        font-size:16px;
        padding-bottom:5px;
        margin-bottom: 5px;
        border-bottom: 1px solid #cfcfcf;
    }
    .wrrite_hd{
        display: flex;
        .hd_fiexd{
            flex:1;
        }
    }
    .input_line{
        display: flex;
        margin:5px 5px;
    }
    .label_name{
        height:32px;
        line-height: 32px;
        font-weight: 500;
        flex: 0 0 100px;
    }
}
</style>